*,
*::before,
*::after {
	box-sizing: inherit;
	margin: 0;
	padding: 0;
} 

html {
	font-size: 62.5%;
} 

body {
	background-color: #ecf0f1;
	color: #d35400;
	font-family: sans-serif;
	font-size: 16px;
	line-height: 1.7rem;
	padding: 3rem;
}

h1 {
	font-size: 6rem;
	margin-top: 5rem;
	text-align: left;
}
